<template>
  <v-select
    class="select"
    :class="{
      select_compact: compact,
      select_error: error
    }"
    :placeholder="placeholder"
    :options="options"
    :reduce="(item) => item.value"
    :value="value"
    :clearable="clearable ?? true"
    :searchable="inputMode"
    :taggable="inputMode"
    @input="onInput"
    @option:selected="onOptionSelected"
    @search="onSearch"
    @close="onClose"
  >
  </v-select>
</template>

<script>
export default {
  props: {
    placeholder: String,
    options: {},
    value: {},
    clearable: Boolean,
    compact: Boolean,
    inputMode: Boolean,
    error: Boolean,
  },
  data() {
    return {
      search: '',
    };
  },
  methods: {
    onSearch(search) {
      this.search = search;
    },
    onInput(value) {
      this.$emit('input', value);
      this.search = '';
    },
    onClose() {
      if (this.inputMode && this.search) {
        this.$emit('input', this.search);
      }
    },
    onOptionSelected(option) {
      if (this.inputMode && option.label) {
        this.$emit('input', option.label);
      }
    },
  },
};
</script>

